<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <img :src="album.blurPicUrl" alt="" />
      </div>
      <div class="text">
        <p>{{ album.name }}</p>
        <i>Album by {{ artist }}</i>
        <i>{{ album.publishTime | dateFormat }}</i>
        <span>{{ album.description | ellipsis }}</span>
      </div>
    </div>
    <hr />
    <div class="main">
      <el-table :data="songs" style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column width="50px">
          <template slot-scope="scope">
            <el-button @click="setData(scope.row)" type="text">
              <img src="../assets/player.png" />
            </el-button>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="歌曲"></el-table-column>
        <el-table-column label="歌手">
          <template slot-scope="scope">
            <router-link
              v-for="(item, index) in scope.row.ar"
              :key="index"
              :to="{ path: `/artist/${item.id}` }"
              >{{ item.name }}
            </router-link>
          </template>
        </el-table-column>
        <el-table-column label="时长">
          <template slot-scope="scope">
            <span>{{ scope.row.dt | timeFormat }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      album: {},
      artist: "",
      songs: [],
    };
  },
  methods: {
    async getAlbum() {
      const { data: res } = await this.$http.get("/album", {
        params: {
          id: this.$route.params.id,
        },
      });
      // console.log(res);
      this.album = res.album;
      this.songs = res.songs;
      this.artist = res.album.artist.name;
    },
    setData(data) {
      // console.log(data);
      this.$emit("getData", data);
    },
  },
  created() {
    this.getAlbum();
  },
  filters: {
    ellipsis(value) {
      if (!value) return "";
      if (value.length > 50) {
        return value.slice(0, 50) + "...";
      }
      return value;
    },
    timeFormat(time) {
      const count = time / 1000;
      const m = (parseInt((count / 60) % 60) + "").padStart(2, "0");
      const s = (parseInt(count % 60) + "").padStart(2, "0");
      return `${m}:${s}`;
    },
    dateFormat(date) {
      const dt = new Date(date);
      const y = dt.getFullYear();
      const m = (dt.getMonth() + 1 + "").padStart(2, "0");
      const d = (dt.getDate() + "").padStart(2, "0");
      return `${y}-${m}-${d}`;
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 20px 50px;
  background-color: rgba(255, 255, 255, 0.5);
  .header {
    height: 320px;
    .photo {
      float: left;
      margin: 30px;
      width: 250px;
      height: 250px;
      img {
        width: 250px;
        height: 250px;
        border-radius: 8%;
      }
    }
    .text {
      float: left;
      margin-left: 20px;
      width: 60%;
      height: 250px;
      p {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC",
          "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        font-size: 30px;
        font-weight: bold;
      }
      i {
        display: block;
        font-family: "Helvetica Neue", Helvetica, "PingFang SC",
          "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        font-size: 18px;
        font-weight: bold;
      }
      span {
        display: block;
        margin-top: 30px;
        font-family: "Helvetica Neue", Helvetica, "PingFang SC",
          "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        font-size: 15px;
      }
    }
  }
  .main {
    .el-table {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      color: black;
      a {
        text-decoration: none;
        color: black;
      }
      img {
        width: 25px;
        height: 25px;
      }
    }
  }
}
</style>