<template>
  <div class="container">
    <div class="main">
      <ul>
        <li v-for="(item, index) in toplists" :key="index">
          <router-link :to="{ path: `/playlist/${item.id}` }">
            <img :src="item.coverImgUrl" />
            <div class="text">
              <p>{{ item.name }}</p>
              <div v-if="item.tracks.length !== 0">
                <span
                  class="tracks"
                  v-for="(items, index) in item.tracks"
                  :key="index"
                  >{{ index + 1 }}.{{ items.first }}-{{ items.second }}</span
                >
              </div>
              <div v-else>
                <span class="update">{{ item.updateFrequency }}</span>
              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      toplists: [],
    };
  },
  methods: {
    async getToplists() {
      const { data: res } = await this.$http.get("/toplist/detail");
      // console.log(res);
      this.toplists = res.list;
    },
  },
  created() {
    this.getToplists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .main {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        margin: 20px;
        width: 430px;
        height: 180px;
        overflow: hidden;
        a {
          text-decoration: none;
          img {
            float: left;
            width: 180px;
            height: 180px;
            border-radius: 8%;
          }
          .text {
            float: left;
            width: 250px;
            height: 180px;
            background-color: #dcdfe6;
            border-radius: 8%;
            p {
              margin: 20px;
              color: black;
              font-family: "Helvetica Neue", Helvetica, "PingFang SC",
                "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
                sans-serif;
              font-size: 25px;
              font-weight: bold;
            }
            .tracks {
              display: block;
              margin: 5px;
              color: black;
              font-family: "Helvetica Neue", Helvetica, "PingFang SC",
                "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
                sans-serif;
              font-size: 15px;
            }
            .update {
              display: block;
              margin: 20px;
              color: black;
              font-family: "Helvetica Neue", Helvetica, "PingFang SC",
                "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
                sans-serif;
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}
</style>