<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <img :src="artist.picUrl" />
        <p>{{ artist.name }}</p>
      </div>
    </div>
    <div class="main">
      <el-tabs value="first" stretch>
        <el-tab-pane label="热门作品" name="first">
          <el-table :data="hotSongs" style="width: 100%">
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column width="50px">
              <template slot-scope="scope">
                <el-button @click="setData(scope.row)" type="text">
                  <img src="../assets/player.png" />
                </el-button>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="歌曲"></el-table-column>
            <el-table-column label="专辑">
              <template slot-scope="scope">
                <router-link
                  :to="{ path: `/album/${scope.row.al.id}` }"
                  >{{ scope.row.al.name }}</router-link
                >
              </template>
            </el-table-column>
            <el-table-column label="时长">
              <template slot-scope="scope">
                <span>{{ scope.row.dt | timeFormat }}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="专辑" name="second">
          <div class="album">
            <ul>
              <li v-for="(item, index) in hotAlbums" :key="index">
                <router-link :to="{ path: `/album/${item.id}` }">
                  <img :src="item.blurPicUrl" alt="" />
                  <span>{{ item.name }}</span>
                </router-link>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <el-tab-pane label="MV" name="third">
          <div class="mv">
            <ul>
              <li v-for="(item, index) in mvs" :key="index">
                <a href="">
                  <img :src="item.imgurl" alt="" />
                  <span>{{ item.name }}</span>
                </a>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <el-tab-pane label="详情" name="fourth">
          <div class="desc">
            <span>{{ artist.briefDesc }}</span>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artist: {},
      hotSongs: [],
      hotAlbums: [],
      mvs: [],
    };
  },
  methods: {
    async getArtist() {
      const { data: res } = await this.$http.get("/artists", {
        params: {
          id: this.$route.params.id,
        },
      });
      // console.log(res);
      this.artist = res.artist;
      this.hotSongs = res.hotSongs;
    },
    async getHotAlbums() {
      const { data: res } = await this.$http.get("/artist/album", {
        params: {
          id: this.$route.params.id,
        },
      });
      // console.log(res);
      this.hotAlbums = res.hotAlbums;
    },
    async getMvs() {
      const { data: res } = await this.$http.get("/artist/mv", {
        params: {
          id: this.$route.params.id,
        },
      });
      // console.log(res);
      this.mvs = res.mvs;
    },
    setData(data) {
      // console.log(data);
      this.$emit("getData", data);
    },
  },
  created() {
    this.getArtist();
    this.getHotAlbums();
    this.getMvs();
  },
  filters: {
    timeFormat(time) {
      const count = time / 1000;
      const m = (parseInt((count / 60) % 60) + "").padStart(2, "0");
      const s = (parseInt(count % 60) + "").padStart(2, "0");
      return `${m}:${s}`;
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 20px 50px;
  background-color: rgba(255, 255, 255, 0.5);
  .header {
    .photo {
      position: relative;
      margin: 30px;
      width: 900px;
      height: 450px;
      overflow: hidden;
      img {
        width: 900px;
        transform: translate(0, -20%);
      }
      p {
        position: absolute;
        left: 50px;
        bottom: 0;
        font-family: "Helvetica Neue", Helvetica, "PingFang SC",
          "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        color: #fff;
        font-size: 50px;
        font-weight: bold;
      }
    }
  }
  .main {
    .el-table {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      color: black;
      a {
        text-decoration: none;
        color: black;
      }
      img {
        width: 25px;
        height: 25px;
      }
    }
    .album {
      ul {
        padding: 0;
        list-style: none;
        li {
          display: inline-block;
          width: 150px;
          height: 200px;
          overflow: hidden;
          margin: 20px;
          a {
            text-decoration: none;
            img {
              width: 150px;
              height: 150px;
              border-radius: 8%;
            }
            span {
              display: block;
              width: 150px;
              color: black;
              font-family: "Helvetica Neue", Helvetica, "PingFang SC",
                "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
                sans-serif;
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
    }
    .mv {
      ul {
        padding: 0;
        list-style: none;
        li {
          display: inline-block;
          width: 240px;
          height: 240px;
          overflow: hidden;
          margin: 20px;
          a {
            text-decoration: none;
            img {
              width: 240px;
              height: 160px;
            }
            span {
              display: block;
              width: 240px;
              color: black;
              font-family: "Helvetica Neue", Helvetica, "PingFang SC",
                "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
                sans-serif;
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
    }
    .desc {
      padding: 20px;
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      font-size: 15px;
    }
  }
}
</style>