<template>
  <div class="container">
    <div class="playlist">
      <div class="title">
        <span>推荐歌单</span>
        <router-link :to="{ path: '/playlists' }">查看全部</router-link>
      </div>
      <div class="main">
        <ul>
          <li v-for="(item, index) in playlists" :key="index">
            <router-link :to="{ path: `/playlist/${item.id}` }">
              <img :src="item.picUrl" />
              <span>{{ item.name }}</span>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="album">
      <div class="title">
        <span>最新专辑 </span>
      </div>
      <div class="main">
        <ul>
          <li v-for="(item, index) in albums" :key="index">
            <router-link :to="{ path: `/album/${item.id}` }">
              <img :src="item.blurPicUrl" alt="" />
              <span>{{ item.name }}</span>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="artist">
      <div class="title">
        <span>热门歌手</span>
        <router-link :to="{ path: '/artists' }">查看全部</router-link>
      </div>
      <div class="main">
        <ul>
          <li v-for="(item, index) in artists" :key="index">
            <router-link :to="{ path: `/artist/${item.id}` }">
              <img :src="item.img1v1Url" />
              <span>{{ item.name }}</span>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="toplist">
      <div class="title">
        <span>排行榜</span>
        <router-link :to="{ path: '/toplists' }">查看全部</router-link>
      </div>
      <div class="main">
        <ul>
          <li
            v-for="item in toplists"
            :key="item.id"
            v-show="item.tracks.length !== 0"
          >
            <router-link :to="{ path: `/playlist/${item.id}` }">
              <img :src="item.coverImgUrl" />
              <div class="text">
                <p class="name">{{ item.name }}</p>
                <span
                  class="tracks"
                  v-for="(items, index) in item.tracks"
                  :key="index"
                  >{{ index + 1 }}.{{ items.first }}-{{ items.second }}</span
                >
              </div>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artists: [],
      playlists: [],
      albums: [],
      toplists: [],
    };
  },
  methods: {
    async getPlaylists() {
      const { data: res } = await this.$http.get("/personalized", {
        params: { limit: 20 },
      });
      // console.log(res);
      this.playlists = res.result;
    },
    async getAlbums() {
      const { data: res } = await this.$http.get("/album/new", {
        params: { limit: 20 },
      });
      // console.log(res);
      this.albums = res.albums;
    },
    async getArtists() {
      const { data: res } = await this.$http.get("/top/artists", {
        params: { limit: 10 },
      });
      // console.log(res);
      this.artists = res.artists;
    },
    async getToplists() {
      const { data: res } = await this.$http.get("/toplist/detail", {});
      // console.log(res);
      this.toplists = res.list;
    },
  },
  created() {
    this.getPlaylists();
    this.getAlbums();
    this.getArtists();
    this.getToplists();
  },
};
</script>

