<template>
  <div class="container">
    <div class="header">
      <el-radio-group v-model="queryInfo.type" @change="handleClick">
        <el-radio-button label="-1">全部</el-radio-button>
        <el-radio-button label="1">男</el-radio-button>
        <el-radio-button label="2">女</el-radio-button>
        <el-radio-button label="3">组合</el-radio-button>
      </el-radio-group>
      <el-radio-group v-model="queryInfo.area" @change="handleClick">
        <el-radio-button label="-1">全部</el-radio-button>
        <el-radio-button label="7">华语</el-radio-button>
        <el-radio-button label="96">欧美</el-radio-button>
        <el-radio-button label="8">日本</el-radio-button>
        <el-radio-button label="16">韩国</el-radio-button>
        <el-radio-button label="0">其他</el-radio-button>
      </el-radio-group>
    </div>
    <hr />
    <div class="main">
      <ul>
        <li v-for="(item, index) in artists" :key="index">
          <router-link :to="{ path: `/artist/${item.id}` }">
            <img :src="item.img1v1Url" />
            <span>{{ item.name }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="footer">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="queryInfo.limit"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      total: 100,
      currentPage: 1,
      artists: [],
      queryInfo: {
        type: "-1",
        area: "-1",
        limit: 20,
        offset: 0,
      },
    };
  },
  methods: {
    async getArtists() {
      const { data: res } = await this.$http.get("/artist/list", {
        params: this.queryInfo,
      });
      // console.log(res);
      this.artists = res.artists;
    },
    handleClick() {
      this.currentPage = 1;
      this.queryInfo.offset = 0;
      this.getArtists();
    },
    handleCurrentChange() {
      this.queryInfo.offset = (this.currentPage - 1) * this.queryInfo.limit;
      this.getArtists();
    },
  },
  created() {
    this.getArtists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    padding: 20px;
    .el-radio-group {
      display: block;
      margin-top: 20px;
    }
  }
  hr {
    border: 1px solid black;
  }
  .main {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        a {
          text-decoration: none;
          img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
          }
          span {
            display: block;
            width: 150px;
            margin-top: 10px;
            text-align: center;
            color: black;
            font-family: "Helvetica Neue", Helvetica, "PingFang SC",
              "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
              sans-serif;
            font-size: 19px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .footer {
    width: 300px;
    margin: 30px auto;
  }
}
</style>