<template>
  <div class="container">
    <div class="header">
      <div>
        <span>{{ queryInfo.cat }}</span>
        <el-button
          type="primary"
          @click="dialogVisible = !dialogVisible"
          plain
          size="medium"
        >
          选择分类<i class="el-icon-arrow-down"></i>
        </el-button>
      </div>
      <div v-show="dialogVisible">
        <el-radio-group v-model="queryInfo.cat" @change="handleClick">
          <el-radio-button
            v-for="(item, index) in categories"
            :key="index"
            :label="item.name"
          ></el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <hr />
    <div class="main">
      <ul>
        <li v-for="(item, index) in playlists" :key="index">
          <router-link :to="{ path: `/playlist/${item.id}` }">
            <img :src="item.coverImgUrl" />
            <span>{{ item.name }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="footer">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="queryInfo.limit"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      flag: true,
      total: 0,
      currentPage: 1,
      categories: [],
      playlists: [],
      queryInfo: {
        cat: "全部",
        limit: 30,
        offset: 0,
      },
      loading: true,
    };
  },
  methods: {
    async getCategories() {
      const { data: res } = await this.$http.get("/playlist/catlist");
      // console.log(res);
      this.categories = res.sub;
    },
    async getPlaylists() {
      this.loading = true;
      const { data: res } = await this.$http.get("/top/playlist", {
        params: this.queryInfo,
      });
      // console.log(res);
      this.playlists = res.playlists;
      this.total = res.total;
      this.loading = false;
    },
    handleClick() {
      this.dialogVisible = false;
      this.currentPage = 1;
      this.queryInfo.offset = 0;
      this.getPlaylists();
    },
    handleCurrentChange() {
      this.queryInfo.offset = (this.currentPage - 1) * this.queryInfo.limit;
      this.getPlaylists();
    },
  },
  created() {
    this.getCategories();
    this.getPlaylists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    padding: 10px 20px;
    span {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      font-size: 30px;
    }
    .el-button {
      float: right;
    }
    .el-radio-group {
      padding: 20px;
      background-color: #ebeef5;
      .el-radio-button {
        margin: 5px;
      }
    }
  }
  hr {
    border: 1px solid black;
  }
  .main {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        &:hover {
          transition: all 0.3s;
          transform: translate(0, -10px);
        }
        a {
          text-decoration: none;
          img {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          span {
            display: block;
            width: 150px;
            color: black;
            font-family: "Helvetica Neue", Helvetica, "PingFang SC",
              "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
              sans-serif;
            font-size: 15px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .footer {
    width: 450px;
    margin: 30px auto;
  }
}
</style>