<template>
  <el-container>
    <el-header>
      <div class="btn">
        <el-button @click="goback" type="text">
          <img src="../assets/left.png" />
        </el-button>
        <el-button @click="goahead" type="text">
          <img src="../assets/right.png" />
        </el-button>
      </div>
      <el-menu
        router
        mode="horizontal"
        text-color="#000"
        active-text-color="#409EFF"
        background-color="#f1f3f4"
      >
        <el-menu-item index="/discover">
          <span>首页</span>
        </el-menu-item>
        <el-menu-item index="/artists">
          <span>歌手</span>
        </el-menu-item>
        <el-menu-item index="/playlists">
          <span>歌单</span>
        </el-menu-item>
        <el-menu-item index="/toplists">
          <span>排行榜</span>
        </el-menu-item>
      </el-menu>
      <el-input placeholder="搜索音乐/歌手/歌单" v-model="keyword" clearable>
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="search"
        ></el-button
      ></el-input>
    </el-header>
    <el-main>
      <router-view @getData="getData"></router-view>
    </el-main>
    <el-footer v-show="id">
      <div class="info">
        <img :src="src" alt="" />
        <div class="text">
          <span>{{ artist }} -- {{ song }}</span>
          <audio :src="url" controls autoplay loop ref="audio"></audio>
        </div>
      </div>
      <div class="control">
        <el-button type="text">
          <img src="../assets/up.png" alt="" />
        </el-button>
        <el-button type="text" @click="pause">
          <img v-if="display" src="../assets/play.png" alt="" />
          <img v-else src="../assets/pause.png" alt="" />
        </el-button>
        <el-button type="text">
          <img src="../assets/down.png" alt="" />
        </el-button>
      </div>
    </el-footer>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      id: "",
      url: "",
      src: "",
      song: "",
      artist: "",
      keyword: "",
      display: false,
    };
  },
  methods: {
    getData(data) {
      // console.log(data);
      this.id = data.id;
      this.song = data.name;
      this.src = data.al.picUrl;
      this.artist = data.ar[0].name;
      this.url = `https://music.163.com/song/media/outer/url?id=${this.id}.mp3`;
      this.display = false;
    },
    search() {
      if (this.keyword) this.$router.push({ path: `/search/${this.keyword}` });
    },
    goback() {
      this.$router.back();
    },
    goahead() {
      this.$router.forward();
    },
    pause() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
      this.display = this.$refs.audio.paused;
    },
  },
};
</script>

