<template>
  <div class="container">
    <el-tabs value="first" stretch>
      <el-tab-pane label="相关歌曲" name="first">
        <div class="music">
          <el-table :data="songs" style="width: 100%">
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column width="50px">
              <template slot-scope="scope">
                <el-button @click="setData(scope.row)" type="text">
                  <img src="../assets/player.png" />
                </el-button>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="歌曲"></el-table-column>
            <el-table-column label="歌手">
              <template slot-scope="scope">
                <router-link
                  v-for="(item, index) in scope.row.ar"
                  :key="index"
                  :to="{ path: `/artist/${item.id}` }"
                  >{{ item.name }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column label="专辑">
              <template slot-scope="scope">
                <router-link :to="{ path: `/album/${scope.row.al.id}` }"
                  >{{ scope.row.al.name }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column label="时长">
              <template slot-scope="scope">
                <span>{{ scope.row.dt | timeFormat }}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane label="相关歌手" name="second">
        <div class="artist">
          <ul>
            <li v-for="(item, index) in artists" :key="index">
              <router-link :to="{ path: `/artist/${item.id}` }">
                <img :src="item.img1v1Url" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
      <el-tab-pane label="相关专辑" name="third">
        <div class="album">
          <ul>
            <li v-for="(item, index) in albums" :key="index">
              <router-link :to="{ path: `/album/${item.id}` }">
                <img :src="item.blurPicUrl" alt="" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
      <el-tab-pane label="相关歌单" name="fourth">
        <div class="playlist">
          <ul>
            <li v-for="(item, index) in playlists" :key="index">
              <router-link :to="{ path: `/playlist/${item.id}` }">
                <img :src="item.coverImgUrl" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      songs: [],
      artists: [],
      playlists: [],
      albums: [],
    };
  },
  methods: {
    async getSongs() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "1",
        },
      });
      // console.log(res);
      this.songs = res.result.songs;
    },
    async getArtists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "100",
        },
      });
      // console.log(res);
      this.artists = res.result.artists;
    },
    async getAlbums() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "10",
        },
      });
      // console.log(res);
      this.albums = res.result.albums;
    },
    async getPlaylists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "1000",
        },
      });
      // console.log(res);
      this.playlists = res.result.playlists;
    },
    setData(data) {
      // console.log(data);
      this.$emit("getData", data);
    },
  },
  created() {
    this.getSongs();
    this.getArtists();
    this.getAlbums();
    this.getPlaylists();
  },
  filters: {
    timeFormat(time) {
      const count = time / 1000;
      const m = (parseInt((count / 60) % 60) + "").padStart(2, "0");
      const s = (parseInt(count % 60) + "").padStart(2, "0");
      return `${m}:${s}`;
    },
  },
  watch: {
    $route: {
      handler() {
        this.getSongs();
        this.getArtists();
        this.getAlbums();
        this.getPlaylists();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 20px 50px;
  background-color: rgba(255, 255, 255, 0.5);
  .music {
    .el-table {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      color: black;
      a {
        text-decoration: none;
        color: black;
      }
      img {
        width: 25px;
        height: 25px;
      }
    }
  }
  .playlist {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        a {
          text-decoration: none;
          img {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          span {
            display: block;
            width: 150px;
            color: black;
            font-family: "Helvetica Neue", Helvetica, "PingFang SC",
              "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
              sans-serif;
            font-size: 15px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .album {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        a {
          text-decoration: none;
          img {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          span {
            display: block;
            width: 150px;
            color: black;
            font-family: "Helvetica Neue", Helvetica, "PingFang SC",
              "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
              sans-serif;
            font-size: 15px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .artist {
    ul {
      padding: 0;
      list-style: none;
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        a {
          text-decoration: none;
          img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
          }
          span {
            display: block;
            width: 150px;
            margin-top: 10px;
            text-align: center;
            color: black;
            font-family: "Helvetica Neue", Helvetica, "PingFang SC",
              "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,
              sans-serif;
            font-size: 19px;
            font-weight: bold;
          }
        }
      }
    }
  }
}
</style>

